@charset "utf-8";
@use "../_base/base" as *;
@use "../_base/mixins" as *;

.header {

    &__wrapper {
        @include flex-items;
        @include container;
        padding: .3rem;
        max-width: 1286px;
    }

    nav ul {
        @include reset-list;
        @include flex-items;
        gap: .75rem;
    }

    &__link {
        @include link;
    }

    &__action {
        @include flex-items;
        gap: .45rem;
    }
}