@charset "utf-8";
@use "variables" as *;

@mixin desktop {

  @media print,
  (min-width: 768px) {
    @content;
  }
}

@mixin laptop {

  @media print,
  (min-width: 768px) and (max-width: 1365.98px) {
    @content;
  }
}

@mixin tablet {

  @media print,
  (min-width: 768px) and (max-width: 1279.98px) {
    @content;
  }
}

@mixin mobile {
  @media only screen and (max-width: 767.98px) {
    @content;
  }
}

@mixin mobile-xs {
  @media only screen and (max-width: 374px) {
    @content;
  }
}

@mixin print {
  @media print {
    @content;
  }
}

@mixin text-wrap {
  word-break: break-word;
}

@mixin text-ellipse($line) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin scroll-hidden {
  &::-webkit-scrollbar {
    width: 0;
    opacity: 0;
  }
}

@mixin scroll-auto-x {
  overflow: auto hidden;
}

@mixin scroll-auto-y {
  overflow: hidden auto;
}

@mixin placeholder {
  &::-webkit-input-placeholder {
    @content;
  }

  &::-ms-input-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-moz-placeholder {
    @content;
  }

  &::placeholder {
    @content;
  }
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin link {
  font-weight: 500;
  font-size: .16rem;
  line-height: 100%;
  text-decoration: none;
}

@mixin flex-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin container {
  width: 100%;
  margin: 0 auto;
}